import React, { useState, useRef, useEffect } from "react";
import myVideo from "../../assets/videos/video.mp4";
import MainHeader from "../../components/Users/MainHeader";

import {
  SectionWrapper,
  Stepblock,
  Titletext,
  GalleryWrapper,
  VideoThumbnail,
  ModalOverlay,
  ModalContent,
  CloseButton,
} from "../../components/Styles/AdviceVideoStyles.js";

import {
  Overlay,
  Popup,
  CloseBtn,
  styles,
  CardImageContainer,
  CardImage,
  TitlePay,
  Form,
  FormGroup,
  Label,
  Input,
  Row,
  ButtonGroup,
  Button,
  Wrapper,
} from "../../components/Styles/MainStyle.js";

import ModuleSideNav from "../../components/Users/ModuleSideNav";
import axios from "axios";
import Alertpopup from "../../components/Alertpopup";
export default function AdviceVideos() {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [allRecordVideo, setAllrecordvideo] = useState([]);
  const [errorMsg, seterrorMsg] = useState("");
  const [selectedVideo, setSelectedVideo] = useState(null);
  const videoRef = useRef();
  const backendpath = "https://blueprintcatalyst.com/api/upload/video";
  const apiUrl = "https://blueprintcatalyst.com/api/user/";
  const storedUsername = localStorage.getItem("UserLoginData");
  const userLogin = JSON.parse(storedUsername);
  useEffect(() => {
    document.title = "Investor Presentation Structure - Expert Advice Video";
  }, []);
  useEffect(() => {
    // Disable right-click
    const handleContextMenu = (e) => e.preventDefault();
    document.addEventListener("contextmenu", handleContextMenu);

    // Disable F12, Ctrl+Shift+I, Ctrl+U
    const handleKeydown = (e) => {
      if (
        e.key === "F12" ||
        (e.ctrlKey && e.shiftKey && e.key === "I") ||
        (e.ctrlKey && e.key === "u")
      ) {
        e.preventDefault();
        // alert("Developer tools are disabled for security reasons.");
      }
    };
    document.addEventListener("keydown", handleKeydown);

    // Detect DevTools opening (approximate)
    const detectDevTools = () => {
      const threshold = 160; // Approximate width/height when DevTools opens
      const widthThreshold = window.outerWidth - window.innerWidth > threshold;
      const heightThreshold =
        window.outerHeight - window.innerHeight > threshold;

      if (widthThreshold || heightThreshold) {
        //alert("Developer tools detected. Access is restricted.");
        // Optionally redirect or close modal
        setIsModalOpen(false);
        setSelectedVideo(null);
      }
    };

    const interval = setInterval(detectDevTools, 1000);

    return () => {
      document.removeEventListener("contextmenu", handleContextMenu);
      document.removeEventListener("keydown", handleKeydown);
      clearInterval(interval);
    };
  }, []);
  const openModal = (video) => {
    const videoKey = `video_${video.id}_views`; // unique key for each video
    const videoPath = video.video;
    setSelectedVideo(`${backendpath}/${videoPath}`);
    videolimitsave(video);
  };

  const videolimitsave = async (video) => {
    let formData = {
      user_id: userLogin.id,
      video_id: video.id,
      limit: video.max_limit,
    };
    try {
      const res = await axios.post(apiUrl + "videolimitsave", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data;
      if (respo.status === "2") {
        seterrorMsg(respo.message);
        setTimeout(() => {
          if (videoRef.current) {
            videoRef.current.pause();
            videoRef.current.currentTime = 0;
          }
          setShowPopup(true);
          seterrorMsg("");
        }, 1500);
      } else {
        seterrorMsg("");
        setIsModalOpen(true);
        setTimeout(() => {
          videoRef.current?.play();
        }, 100);
      }
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };

  const closeModal = () => {
    setIsModalOpen(false);
    console.log(videoRef.current);
    if (videoRef.current) {
      videoRef.current.pause();
      videoRef.current.currentTime = 0;
    }
  };

  const apiUrlVideo = "https://blueprintcatalyst.com/api/admin/video/";
  useEffect(() => {
    getvideo();
  }, []);
  const getvideo = async () => {
    let formData = {
      user_id: "",
    };
    try {
      const res = await axios.post(apiUrlVideo + "getvideolist", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      setAllrecordvideo(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const [showPopup, setShowPopup] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();
    // handle form submission
    console.log(e.target);
    setShowPopup(false);
  };
  return (
    <>
      <MainHeader />
      <SectionWrapper className="d-block py-5">
        <div className="container-lg">
          <div className="row justify-content-center">
            <div className="col-md-3">
              <ModuleSideNav />
            </div>

            <div className="col-md-9">
              {errorMsg && (
                <Alertpopup
                  message={errorMsg}
                  onClose={() => seterrorMsg("")}
                />
              )}
              <Stepblock>
                <div className="d-flex flex-column gap-5">
                  <Titletext>
                    investor presentation structure - expert advice videos
                  </Titletext>
                  <GalleryWrapper>
                    {allRecordVideo.map((video, index) => {
                      const videoPath = video.video.replace(/\\/g, "/");

                      return (
                        <VideoThumbnail
                          key={index}
                          onClick={() => openModal(video)}
                        >
                          <video
                            muted
                            onContextMenu={(e) => e.preventDefault()}
                            onError={(e) =>
                              console.error("Error loading video:", e)
                            }
                          >
                            <source
                              src={`${backendpath}/${videoPath}`}
                              type="video/mp4"
                            />
                            <p>
                              Your browser does not support the video tag or the
                              video format.
                            </p>
                          </video>
                        </VideoThumbnail>
                      );
                    })}
                  </GalleryWrapper>
                </div>
              </Stepblock>
            </div>
          </div>
        </div>
      </SectionWrapper>

      <ModalOverlay open={isModalOpen}>
        <ModalContent>
          {selectedVideo && (
            <video
              ref={videoRef}
              className="advicevideo"
              controls
              autoPlay
              controlsList="nodownload nofullscreen noremoteplayback"
              onContextMenu={(e) => e.preventDefault()}
              style={{ width: "100%" }}
            >
              <source src={selectedVideo} type="video/mp4" />
              Your browser does not support the video tag.
            </video>
          )}
          <CloseButton onClick={closeModal}>&times;</CloseButton>
        </ModalContent>
      </ModalOverlay>
      <Wrapper>
        {showPopup && (
          <Overlay>
            <Popup>
              <CloseBtn onClick={() => setShowPopup(false)}>×</CloseBtn>
              <TitlePay>Payment Details</TitlePay>
              <CardImageContainer>
                <CardImage
                  src="/assets/user/images/cardimage.jpg"
                  alt="cards"
                />
              </CardImageContainer>
              <Form onSubmit={handleSubmit}>
                <FormGroup>
                  <Label>Name</Label>
                  <Input type="text" name="name" required placeholder="name" />
                </FormGroup>
                <FormGroup>
                  <Label>Email</Label>
                  <Input
                    type="email"
                    name="email"
                    required
                    placeholder="email"
                  />
                </FormGroup>
                <FormGroup>
                  <Label>Card Number</Label>
                  <Input
                    type="text"
                    name="cardNumber"
                    required
                    placeholder="card number"
                    inputMode="numeric"
                    maxLength={19} // 16 digits + 3 spaces
                    onInput={(e) => {
                      let value = e.target.value.replace(/\D/g, ""); // Remove non-digits
                      value = value.substring(0, 16); // Max 16 digits
                      value = value.replace(/(.{4})/g, "$1 ").trim(); // Add space every 4 digits
                      e.target.value = value;
                    }}
                  />
                </FormGroup>

                <Row>
                  <FormGroup>
                    <Label>Expiry Date</Label>
                    <Input
                      type="text"
                      name="expiry"
                      required
                      placeholder="MM/YYYY"
                      inputMode="numeric"
                      maxLength={7}
                      pattern="(0[1-9]|1[0-2])\/\d{4}" // MM/YYYY format
                      onInput={(e) => {
                        let value = e.target.value.replace(/\D/g, ""); // remove non-digits
                        if (value.length > 6) value = value.slice(0, 6);
                        if (value.length >= 3) {
                          value = value.slice(0, 2) + "/" + value.slice(2);
                        }
                        e.target.value = value;
                      }}
                    />
                  </FormGroup>

                  <FormGroup>
                    <Label>CVV</Label>
                    <Input type="text" name="cvv" required placeholder="123" />
                  </FormGroup>
                </Row>
                <ButtonGroup>
                  <Button
                    type="button"
                    className="cancel"
                    onClick={() => setShowPopup(false)}
                  >
                    Cancel
                  </Button>
                  <Button type="submit" className="submit">
                    Pay Now
                  </Button>
                </ButtonGroup>
              </Form>
            </Popup>
          </Overlay>
        )}
      </Wrapper>
    </>
  );
}
